
.navbar {
  height: 50px;
  line-height: 50px;
  border-radius: 0px !important;
  /*.hideSidebar .fa-bars{
            display: none;
        }*/
}
.navbar .fa-bars {
    cursor: pointer;
    line-height: 50px;
    height: 50px;
    float: left;
    padding: 0 15px;
}
.navbar .fa-bars[isactive] {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
.navbar .hamburger-container {
    line-height: 50px;
    height: 50px;
    float: left;
    padding: 0 10px;
}
.navbar .errLog-container {
    display: inline-block;
    position: absolute;
    right: 150px;
}
.navbar .screenfull {
    position: absolute;
    right: 90px;
    top: 16px;
    color: red;
}
.navbar .avatar-container {
    height: 50px;
    display: inline-block;
    position: absolute;
    right: 35px;
}
.navbar .avatar-container .avatar-wrapper {
      cursor: pointer;
      /* margin-top:5px;*/
      padding: 5px;
      position: relative;
      height: 40px;
      line-height: 40px;
}
.navbar .avatar-container .avatar-wrapper .user-name {
        float: left;
        margin-right: 5px;
}
.navbar .avatar-container .avatar-wrapper .user-avatar {
        width: 40px;
        height: 40px;
        border-radius: 50%;
}
.navbar .avatar-container .avatar-wrapper .el-icon-caret-bottom {
        position: absolute;
        right: -20px;
        top: 25px;
        font-size: 12px;
}
.user-dropdown .active {
  color: red;
}



